:root {
  --select-color-primary: #ff6b01;
}

.modal {
  z-index: 1;
  width: 100%;
  height: 100%;
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.modal-content {
  width: 60%;
  background-color: #fefefe;
  border: 1px solid #888;
  margin: 15% auto;
  padding: 40px 12px;
}

.modal-content img {
  width: 375px;
  height: 478px;
}

.radio-home {
  visibility: hidden;
}

.is-hidden {
  display: none;
}

.header.header-home {
  background-image: url("home-bg@x1.ecc0f976.jpg");
  background-size: 100% 320px;
}

.header.header-lib {
  background-image: url("lib-bg@x1.bcd6755c.jpg");
  background-size: cover;
}

.header {
  max-width: 1280px;
  height: 200px;
  background-position: center;
  background-repeat: no-repeat;
  align-items: center;
  margin: auto;
  padding: 40px 20px;
}

.header a {
  color: #fff;
  text-transform: uppercase;
  font-size: 14px;
  text-decoration: none;
}

.header__navbar {
  justify-content: space-between;
  align-items: center;
  text-decoration: none;
  list-style: none;
  display: flex;
}

.header__navbar ul {
  margin-block: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header__navbar ul li:last-child {
  margin-left: 40px;
}

.header__logo {
  align-content: center;
  display: flex;
}

.header__logo--text {
  margin-block: 0;
  margin-left: 10px;
  display: none;
}

.header__form {
  margin-top: 120px;
  position: relative;
}

.header__form input {
  color: #fff;
  width: 97%;
  background-color: #0000;
  border: none;
  border-bottom: .5px solid #fff;
  padding-bottom: 4px;
  font-size: 14px;
}

.header__form input::placeholder {
  color: #fff;
  opacity: .7;
}

.header__form input:focus {
  outline: none;
}

.header__form--btn {
  cursor: pointer;
  background-color: #0000;
  border: none;
  position: absolute;
  right: 10px;
}

.header__caution {
  height: 25%;
  color: var(--select-color-primary);
  margin-top: 15px;
}

.header__radio {
  justify-content: center;
  margin-top: 100px;
  display: flex;
}

.header__radio--btn {
  width: 100px;
  height: 40px;
  text-align: center;
  text-transform: uppercase;
  margin: 0 5px 0 0;
  font-weight: 700;
  position: relative;
}

.header__radio label, .header__radio input {
  cursor: pointer;
  display: block;
  position: absolute;
  inset: 0;
}

.header__radio input[type="radio"] {
  opacity: 0;
  z-index: 100;
}

.header__radio label {
  z-index: 90;
  color: #fff;
  border: 1px solid #ffffff6e;
  border-radius: 4px;
  line-height: 2.4em;
}

.header__radio input[type="radio"]:checked + label {
  background: orange;
  border: none;
  border-radius: 4px;
}

.pagi-component {
  justify-content: center;
  display: flex;
}

.pagi-component:nth-child(1) {
  margin-right: 5px;
}

.pagi-component a {
  color: #000;
  text-decoration: none;
}

.pagi-component .tui-page-btn {
  border: .5px solid #676464;
  border-radius: 3px;
  padding: 5px;
}

.pagi-component .tui-page-btn.tui-is-selected {
  background-color: orange;
}

.pagi-component .tui-page-btn.tui-is-disabled {
  color: #bbb1b1;
}

.main-container {
  max-width: 1280px;
  margin: auto;
}

#movies {
  flex-wrap: wrap;
  justify-content: space-around;
  gap: 10px;
  padding-block: 40px;
  display: flex;
}

#movies img {
  width: 395px;
  height: 574px;
  transform: scale 2s;
  border-radius: 4px;
}

#movies img:hover {
  transform: scale(1.02);
  box-shadow: 0 -4px 22px -10px #25252a;
}

#movies .movieElement {
  cursor: pointer;
}

